import listCell from '@/components/list-cell'
export default {
	name: 'mineList',
	props: {
		user_info: Object
	},
	data () {
		return {
			list: [
				{title: '我的钱包'},
				{title: '我的评价'},
				{title: '我的收藏'},
				{title: '地址管理'},
				{title: '服务商/设计师认证', approve: 0},
				{title: '投诉/反馈'}
			]
		}
	},
	watch: {
		user_info () {
			this.$set(this.list[4], 'approve', this.user_info.firm)
		}
	},
	methods: {
		onGoToPage (title) {
			this.$emit('onGoToPage', title)
		}
	},
	components: {
		listCell
	}
}
